<template>
  <div class="cart">
    <div class="top">
      <h3 class="title">购物车</h3>
      <div class="delete" @click="removeItem">删除</div>
    </div>
    <div class="total">共{{ rows.length }}件宝贝</div>
    <div class="item_wrapper">
      <div
        class="item"
        :class="{ active_item: rows[index].choose != false }"
        v-for="(item, index) in rows"
        :key="item.id"
        
      >
        <div class="choose" @click="choose(index)">
          <img
            v-if="rows[index].choose == false"
            src="data:image/png;base64,"
            alt=""
          />
          <img
            v-else
            src="data:image/png;base64,"
            alt=""
          />
        </div>

        <img @click="handleCheckDetail(item.id)" :src="item.cover" alt="" />
        <div @click="handleCheckDetail(item.id)" class="miaoshu">
          <div class="summary">
            {{ item.title }}
          </div>
          <div class="label_wrapper">
            <div class="label" v-for="label in item.label" :key="label">
              {{ label }}
            </div>
          </div>
          <div class="cat_wrapper">
            <div class="cat" v-for="cat in item.cat" :key="cat">
              {{ cat }}
            </div>
          </div>
          <div class="buy">
            <div class="price">￥{{ item.price }}</div>
            <div class="number">
              <img
                @click="jian(index)"
                class="jian"
                src="https://img.alicdn.com/tfs/TB1I05Qe1uSBuNjSsplXXbe8pXa-45-40.png?getAvatar=avatar_50x50q90_.webp"
                alt=""
              />
              <input @change="changeNumber(index)" v-model="item.number" />
              <img
                @click="jia(index)"
                class="jia"
                src="https://img.alicdn.com/tfs/TB12tL6ThTpK1RjSZFKXXa2wXXa-45-40.png?getAvatar=avatar_50x50q90_.webp"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="bottom_content">
        <div class="bottom_left">
          <img
            @click="chooseAll"
            v-if="!allChoose()"
            src="data:image/png;base64,"
            alt=""
          />
          <img
            @click="RemoveAll"
            v-else
            src="data:image/png;base64,"
            alt=""
          />
          <div>全选</div>
        </div>
        <div class="bottom_right">
          <span class="sum">合计:</span>
          <span class="sum_price">￥{{ sumPrice() }}</span>
          <span class="jiesuan" :class="{ jiesuan_disabled: !IshaveGood() }"
            >结算({{ IshaveGood() }})</span
          >
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Cart",
  beforeRouteEnter: (to, from, next) => {
    let isLogin = localStorage.getItem("isLogin");
    if (isLogin) next();
    else next("/login");
  },
  components: {},
  created() {
    this.$axios.post("/api/cart/list").then((res) => {
      this.rows = res.data.rows;
      this.setNumber();
    });
  },
  data() {
    return {
      rows: [],
    };
  },
  methods: {
        handleCheckDetail(id) {
      this.$router.push({ name: "Detail", query: { id } });
    },
    //改变input数字
    changeNumber(index) {
      var number = document.getElementsByTagName("input")[index].value;
      this.rows[index].number = parseInt(number);
      this.sumPrice();
      this.$forceUpdate();
    },
    //判断是否全选
    allChoose() {
      for (var i in this.rows) {
        if (this.rows[i].choose == false) {
          return false;
        }
      }
      if (this.rows.length == 0) {
        return false;
      }
      return true;
    },
    //判断是否有商品选中
    IshaveGood() {
      var sum = 0;
      for (var i of this.rows) {
        if (i.choose == true) {
          sum++;
        }
      }
      return sum;
    },
    //切换选中
    choose(index) {
      this.rows[index].choose = !this.rows[index].choose;
      this.$forceUpdate();
    },
    setNumber() {
      for (var i of this.rows) {
        i["number"] = 1;
        i["choose"] = false;
      }
    },
    //加减商品数量
    jian(index) {
      if (this.rows[index].number >= 2) {
        this.rows[index].number--;
      }
      this.$forceUpdate();
    },
    jia(index) {
      this.rows[index].number++;
      this.$forceUpdate();
    },
    //删除商品
    removeItem() {
      let temp = [];
      let ids = [];
      for (let i in this.rows) {
        if (this.rows[i].choose == true) {
          temp.push(i);
          ids.push(this.rows[i].id);
        }
        
      }
      
      this.$axios.post("/api/cart/remove", { ids }).then(() => {
        temp = temp.reverse();
        for (var j of temp) {
          this.rows.splice(j, 1);
        }
      });

      this.$forceUpdate();
    },
    //计算商品价格
    sumPrice() {
      var sum = 0;
      for (var i in this.rows) {
        if (this.rows[i].choose == true) {
          sum += this.rows[i].price * this.rows[i].number;
        }
      }
      return parseFloat(sum).toFixed(2);
    },
    //全选
    chooseAll() {
      for (var i in this.rows) {
        this.rows[i].choose = true;
      }
      this.allChoose();
      this.$forceUpdate();
    },
    //全不选
    RemoveAll() {
      for (var i in this.rows) {
        this.rows[i].choose = false;
      }
      this.allChoose();
      this.$forceUpdate();
    },
  },
};
</script>
<style lang="less">
.cart {
  background-color: #f4f4f4;
  .top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    line-height: 22px;
    margin: 0 2vw;
    margin-top: 8px;
    color: black;
    .title {
      font-size: 20px;
      font-weight: 500;
    }
    .delete {
      font-size: 16px;
    }
  }
  .total {
    color: gray;
    margin: 0 2vw;
    margin-top: 10px;
    font-size: 14px;
  }
  .item_wrapper {
    display: flex;
    flex-direction: column;
    border: 0px solid black;
    position: relative;
    box-sizing: border-box;
    display: flex;
    -webkit-box-orient: vertical;
    flex-direction: column;
    align-content: flex-start;
    flex-shrink: 0;
    .active_item {
      background-color: #f9f9f9 !important;
    }
    .item {
      margin: 0 auto;
      width: 94vw;
      // padding: 6px 0;
      border-radius: 12px;
      background-color: white;
      margin: 8px auto;
      display: flex;
      flex-direction: row;
      align-items: center;
      .choose {
        width: 12vw;
        display: flex;
        height: 28vw;
        padding: 0 3vw;
        align-items: center;
        justify-content: center;
        img {
          width: 5.5vw;
          height: 5.5vw;
        }
      }

      img:nth-child(2) {
        width: 28vw;
        border-radius: 10px;
        // height: 90px;
        background-color: rgba(0, 0, 0, 0.02);
      }
      .miaoshu {
        display: flex;
        flex-direction: column;
        margin-top: 5px;
        margin-left: 12px;
        align-self: start;
        .summary {
          width: 95%;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          font-size: 15px;
          -webkit-box-orient: vertical;
          line-height: 5.5vw;
          color: rgb(51, 51, 51);
        }
        .label_wrapper {
          margin: 6px 0;
          display: flex;
          flex-direction: row;
          .label {
            color: rgb(219, 14, 15);
            margin: 0 2px;
            font-size: 13px;
          }
        }
        .cat_wrapper {
          display: flex;
          width: fit-content;
          flex-direction: row;
          color: rgb(153, 153, 153);
          margin: 4px 2px;
          height: auto;
          padding: 3px;
          border-radius: 3px;
          background-color: rgba(236, 236, 236, 0.298);
          .cat {
            margin: 0 2px;
            font-size: 13px;
          }
        }
        .buy {
          align-items: center;
          display: flex;
          justify-content: space-between;
          flex-direction: row;
          .price {
            color: rgb(219, 14, 15);
            font-size: 14px;
          }
          .number {
            margin: 10px;
            display: flex;
            flex-direction: row;
            .jian,
            .jia {
              width: 25px;
              height: 22px;
            }
            input {
              outline: none;
              border: 1px solid rgb(240, 240, 240);
              width: 30px;
              display: flex;
              align-items: center;
              justify-content: center;
              text-align: center;
            }
          }
        }
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 55px;
    width: 100vw;
    height: 50px;
    background-color: white;
    border-bottom: 1px solid #e7e7e7;
    .bottom_content {
      width: 95%;
      margin: 0 auto;
      height: 100%;

      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .bottom_left {
        display: flex;
        flex-direction: row;
        align-items: center;
        div {
          font-size: 12px;
          color: #666;
          line-height: 20px;
          margin-left: 6px;
        }
      }
      .bottom_right {
        .sum {
          font-size: 14px;
        }
        .sum_price {
          color: #db0e0f;
          font-size: 14px;
        }
        .jiesuan_disabled {
          color: rgba(255, 255, 255, 0.5) !important;
        }
        .jiesuan {
          background-image: linear-gradient(90deg, #ff9900 0%, #db0e0f 98%);
          font-size: 15px;
          line-height: 15px;
          padding: 8px 18px;
          text-align: center;
          border-radius: 80px;
          color: #ffffff;
          white-space: nowrap;
          margin-left: 10px;
        }
      }
    }

    img {
      width: 5.5vw;
      height: 5.5vw;
    }
  }
}
</style>